import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../api/http.service';
@Component({
  selector: 'app-forgot',
  templateUrl: './forgot.page.html',
  styleUrls: ['./forgot.page.scss'],
  providers: [HttpService]
})
export class ForgotPage implements OnInit {

  email:any;
  response:any;
  constructor(public shared: HttpService) { }

  ngOnInit() {
  }


  
  reset(){

    if (this.email ==null || this.email =="" || this.email == undefined){
      this.shared.displayNetworkUpdate('Email Cannot be blank')
    }else{

      let data= this.validateEmail(this.email)

      if (data==false){
        this.shared.displayNetworkUpdate('Please check your email ID')
      }else{

      if (navigator.onLine) {
        this.shared.ServiceCallforgot.ServiceCallRequestForgotData(this.email).then((res) => { 
          this.response=res;
          this.shared.displayNetworkUpdate(this.response.content)
          }, err => {
       
          })
    
      }else{
        this.shared.displayNetworkUpdate('No Internet Connection')
    
      }
    }
    }


  }


  validateEmail(email) 
{
  var re = /^(?:[a-z0-9!#$%&amp;'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&amp;'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])$/;
  return re.test(email);
}


}
